html {
  padding: 0px;
  margin: 0px;
  width: 100vw;
  height: 100vh;
}

.app {
  width: 100%;
  height: 100%;
}

textarea {
  width: 100%;
  border: 0;
  min-height: 300px;
  overflow: visible;
  box-sizing: border-box;
  padding: 10px 0;
  line-height: 40px;
  resize: none;
  font-size: larger;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  background-image: linear-gradient(
    0deg,
    #b2a2cd 2.38%,
    transparent 2.38%,
    transparent 50%,
    #b2a2cd 50%,
    #b2a2cd 52.38%,
    transparent 52.38%,
    transparent 100%
  );
  background-size: 80px 80px;
}
